const Gallery = () => {
    const [filter, setFilter] = useState('Tümü');
    const [selectedImage, setSelectedImage] = useState<typeof galleryData[0] | null>(null);

    useEffect(() => {
        window.scrollTo(0, 0);
    }, []);

    const filteredProjects = filter === 'Tümü'
        ? galleryData
        : galleryData.filter(p => p.category === filter);

    return (
        <div className="min-h-screen bg-[#0A0A0A] text-white pt-32 pb-20 px-4 sm:px-6">
            <SEO
                title="Proje Galerisi | Villa & Rezidans Dekorasyon"
                description="Lavora Design'ın tamamlanmış lüks konut ve ofis projelerini inceleyin. İstanbul'da villa, rezidans ve ofisler için özel iç mekan tasarımları."
                keywords="villa dekorasyon, rezidans iç mimari, lüks ofis tasarımı, özel proje galeri, istanbul iç mimar"
            />
            <Navbar />

            <div className="w-full max-w-7xl mx-auto">
                {/* Header */}
                <div className="text-center mb-16">
                    <motion.span
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        className="text-gold-DEFAULT text-xs font-bold tracking-[0.3em] uppercase mb-4 block"
                    >
                        PORTFOLYO
                    </motion.span>
                    <motion.h1
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ delay: 0.1 }}
                        className="text-4xl md:text-6xl font-playfair text-white mb-6"
                    >
                        Özel Projeler
                    </motion.h1>
                    <div className="w-24 h-[1px] bg-gold-DEFAULT mx-auto opacity-50"></div>
                </div>

                {/* Filters */}
                <div className="flex justify-center flex-wrap gap-6 sm:gap-8 mb-16">
                    {categories.map(cat => (
                        <button
                            key={cat}
                            onClick={() => setFilter(cat)}
                            className={`text - xs sm: text - sm uppercase tracking - [0.2em] transition - all duration - 300 relative group py - 2 font - medium
                        ${filter === cat ? 'text-gold-DEFAULT' : 'text-white/40 hover:text-white'} `}
                        >
                            {cat}
                            <span className={`absolute bottom - 0 left - 0 w - full h - [1px] bg - gold - DEFAULT transform origin - left transition - transform duration - 300 ${filter === cat ? 'scale-x-100' : 'scale-x-0 group-hover:scale-x-50'} `}></span>
                        </button>
                    ))}
                </div>

                {/* Masonry Grid - 2 Column Mobile, 3 Mid, 4 Desktop */}
                <motion.div layout className="columns-2 sm:columns-3 lg:columns-4 gap-4 space-y-4">
                    <AnimatePresence>
                        {filteredProjects.map(project => (
                            <motion.div
                                key={project.id}
                                layout
                                initial={{ opacity: 0, scale: 0.95 }}
                                animate={{ opacity: 1, scale: 1 }}
                                exit={{ opacity: 0, scale: 0.9 }}
                                transition={{ duration: 0.6, ease: [0.32, 0.72, 0, 1] }}
                                className={`break-inside - avoid group relative rounded - sm overflow - hidden cursor - pointer ${project.height} mb - 4`}
                                onClick={() => setSelectedImage(project)}
                            >
                                <img
                                    src={project.image}
                                    alt={project.title}
                                    className="w-full h-full object-cover transition-transform duration-[1.5s] ease-out group-hover:scale-110"
                                />
                                {/* Hover Overlay */}
                                <div className="absolute inset-0 bg-black/50 opacity-0 group-hover:opacity-100 transition-opacity duration-500 flex items-center justify-center">
                                    <div className="text-center transform translate-y-4 group-hover:translate-y-0 transition-transform duration-500">
                                        <h3 className="text-lg sm:text-xl font-playfair text-white mb-2 italic px-4">{project.title}</h3>
                                        <p className="text-[10px] uppercase tracking-widest text-gold-DEFAULT">İncele</p>
                                    </div>
                                </div>
                            </motion.div>
                        ))}
                    </AnimatePresence>
                </motion.div>
            </div>

            {/* Lightbox */}
            {selectedImage && createPortal(
                <AnimatePresence>
                    <motion.div
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        exit={{ opacity: 0 }}
                        className="fixed inset-0 z-[9999] bg-black/98 flex items-center justify-center p-4"
                        onClick={() => setSelectedImage(null)}
                    >
                        <button className="absolute top-6 sm:top-8 right-6 sm:right-8 z-[110] text-white/50 hover:text-white transition-colors group">
                            <span className="text-[10px] uppercase tracking-widest mr-2 opacity-0 group-hover:opacity-100 transition-opacity hidden sm:inline">Kapat</span>
                            <i className="ri-close-line text-3xl sm:text-4xl"></i>
                        </button>
                        <motion.img
                            initial={{ scale: 0.95, opacity: 0 }}
                            animate={{ scale: 1, opacity: 1 }}
                            transition={{ duration: 0.5, ease: "easeOut" }}
                            src={selectedImage.image}
                            alt={selectedImage.title}
                            className="max-h-[85vh] max-w-[95vw] object-contain shadow-2xl shadow-black/50 border border-white/5"
                            onClick={(e) => e.stopPropagation()}
                        />
                        <div className="absolute bottom-6 sm:bottom-10 left-6 sm:left-10 text-left z-[110] pointer-events-none">
                            <p className="text-gold-DEFAULT text-xs tracking-[0.3em] uppercase mb-2">{selectedImage.category}</p>
                            <h3 className="text-2xl sm:text-3xl md:text-4xl font-playfair text-white font-light">{selectedImage.title}</h3>
                        </div>
                    </motion.div>
                </AnimatePresence>,
                document.body
            )}

            <Footer />
        </div>
    );
};

export default Gallery;
